import { useState } from 'react';
import { useApp } from "./useApp.js";
import deleteLastSpace from '../functions/deleteLastSpace.js';
import { DialogWindow } from './general/DialogWindow.js';


//mui
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import { Typography } from '@mui/material';


export default function P_FormWbsEdit({ setShow, isProject, setIsProject, selectedWbs, setSelectedWbs }) {

  // proje ve _id si yoksa wbs oluturma formunu gstermenin bir anlam yok, hata vererek durduruyoruz
  if (!isProject?._id) {
    throw new Error("Wbs oluturulacak projenin database kayd iin ProjeId belirtilmemi, sayfay yeniden ykleyin, sorun devam ederse Rapor7/24 ile irtibata geiniz.")
  }

  const [showDialog, setShowDialog] = useState(false)
  const [dialogCase, setDialogCase] = useState("")

  const [error_for_wbsName, setError_for_wbsName] = useState(false)
  const [errorText_for_wbsName, setErrorText_for_wbsName] = useState()

  const [error_for_wbsCodeName, setError_for_wbsCodeName] = useState(false)
  const [errorText_for_wbsCodeName, setErrorText_for_wbsCodeName] = useState()

  const RealmApp = useApp();

  const callBack_m = () => setShow()



  async function handleSubmit(event) {

    event.preventDefault();
    let isError = false

    try {

      // girilen verileri alma ve sonlarndaki boluklar kaldrma
      const data = new FormData(event.currentTarget);
      const wbsName = deleteLastSpace(data.get('wbsName'))
      const wbsCodeName = deleteLastSpace(data.get('wbsCodeName'))

      // bu ksmda frontend ksmnda form validation hatalarn ilgili alanlarda gsterme ilemleri yaplr, aada backend de
      if (!wbsName) {
        setError_for_wbsName(true);
        setErrorText_for_wbsName("Zorunlu")
        isError = true
        console.log("wbsName", "yok -- error")
      }

      // bu ksmda frontend ksmnda form validation hatalarn ilgili alanlarda gsterme ilemleri yaplr, aada backend de
      if (!wbsCodeName) {
        setError_for_wbsCodeName(true);
        setErrorText_for_wbsCodeName("Zorunlu")
        isError = true
        console.log("wbsCodeName", "yok -- error")
      }

      if (wbsCodeName.includes(" ") ) {
        setError_for_wbsCodeName(true);
        setErrorText_for_wbsCodeName("Boluk kullanmaynz")
        isError = true
        console.log("wbsCodeName", "yok -- error")
      }

      // ilgili hatalar yukarda ilgili form alanlarna yazlm olmal
      // db ye sorgu yaplp db megul edilmesin diye burada durduruyoruz
      // frontendden gese bile db den errorFormObject kontrol yaplyor aada
      if (isError) {
        console.log("bu satrn altnda fonksiyon --return-- ile durduruldu")
        return
      }



      // yukardaki yaplan _id kontrol tamamsa bu veri db de kaydolmu demektir, refetch_pozlar() yapp db yi yormaya gerek yok
      // useQuery ile oluturduumuz pozlar cash datamz gncelliyoruz
      // sorgudan wbs datas gncellenmi proje ddrp, gelen data ile aada react useContext deki projeyi update ediyoruz
      const newWbsItem = {
        projectId: isProject._id,
        wbsId:selectedWbs._id,
        newWbsName: wbsName,
        newWbsCodeName: wbsCodeName
      }

      const result = await RealmApp.currentUser.callFunction("updateWbs", newWbsItem);

      // eer gnderilen form verilerinde hata varsa db den gelen form validation mesajlar form iindeki ilgili alanlarda gsterilir ve fonksiyon durdurulur
      // yukarda da frontend kontrol yaplmt
      if (result.errorFormObj) {

        const errorFormObj = result.errorFormObj

        console.log("errorFormObj", errorFormObj)

        // baka form alanlar olsayd onlarnkini de ekleyecektik aadaki returnden nce, onlarda da hata uyarlarn grecektik
        if (errorFormObj.newWbsName) {
          setError_for_wbsName(true);
          setErrorText_for_wbsName(errorFormObj.newWbsName)
          isError = true
        }

        // baka form alanlar olsayd onlarnkini de ekleyecektik aadaki returnden nce, onlarda da hata uyarlarn grecektik
        if (errorFormObj.newWbsCodeName) {
          setError_for_wbsCodeName(true);
          setErrorText_for_wbsCodeName(errorFormObj.newWbsCodeName)
          isError = true
        }

        return
      }


      // _id yoksa istediimiz proje verisi deil demekki, hata ile durduruyoruz
      if (!result.project._id) {
        throw new Error("db den Proje olarak beklenen verinin _id property yok, sayfay yenileyiniz, sorun devam ederse Rapor7/24 ile irtibata geiniz..")
      }


      // yukardaki yaplan _id kontrol tamamsa bu veri db de kaydolmu demektir, refetch_pozlar() yapp db yi yormaya gerek yok
      // useQuery ile oluturduumuz pozlar cash datamz gncelliyoruz
      setIsProject(result.project)

      // sorgu ileminden nce seilen wbs varsa, temizliyoruz, en byk gereke seilen wbs silinmi olabilir, onunla ilem db de hata verir
      setSelectedWbs(null)

      setShow()

      return

      // setShowDialogSuccess("Wbs kayd baar ile gerekleti")

    } catch (err) {

      console.log(err)
      let hataMesaj_ = err?.message ? err.message : "Beklenmedik hata, sayfay yenileyiniz, sorun devam ederse Rapor7/24 ile irtibata geiniz.."

      let text1 = "__mesajBaslangic__"
      let text2 = "__mesajBitis__"
      let mesajBaslangic = hataMesaj_.includes(text1) ? hataMesaj_.indexOf(text1) + text1.length : 0
      let mesajBitis = hataMesaj_.includes(text2) ? hataMesaj_.indexOf(text2) : hataMesaj_.length
      // console.log(hataMesaj_.slice(mesajBaslangic + "mesajBaslangic:".length, mesajBitis))
      hataMesaj_ = hataMesaj_.slice(mesajBaslangic, mesajBitis)
      console.log(hataMesaj_)

      // eer ifte kayt oluyorsa form iindeki poz ismi girilen yere aadaki mesaj gnderilir, fonksiyon durdurulur
      // form sayfas kapanmadan hata gsterimi
      if (hataMesaj_.includes("duplicate key error")) {
        setError_for_wbsName(true);
        setErrorText_for_wbsName("Ayn seviyede, ayn isimde wbs olamaz")
        console.log("Ayn seviyede, ayn isimde wbs olamaz")
        return
      }

      setDialogCase("error")
      setShowDialog(hataMesaj_)

    }

  }


  return (
    <div>

      {showDialog &&
        <DialogWindow dialogCase={dialogCase} showDialog={showDialog} setShowDialog={setShowDialog} afterDone={callBack_m} />
      }

      <Dialog
        PaperProps={{ sx: { width: "80%", position: "fixed", top: "10rem" } }}
        open={true}
        onClose={() => setShow()} >
        {/* <DialogTitle>Subscribe</DialogTitle> */}
        <Box component="form" onSubmit={handleSubmit} noValidate sx={{ mt: 1 }}>

          <DialogContent>

            <DialogContentText sx={{ fontWeight: "bold", paddingBottom: "1rem" }}>
              {/* <Typography sx> */}
              Wbs Gncele
              {/* </Typography> */}
            </DialogContentText>

            {/* {selectedWbs &&
              <>
                <DialogContentText sx={{ fontWeight: "bold", paddingBottom: "1rem" }}>
                  {selectedWbs.code} {"-->"} {selectedWbs.name}
                </DialogContentText>
                <Typography >
                  bal altna yeni bir Wbs eklemek zeresiniz.
                </Typography>
              </>
            } */}

            {/* {!selectedWbs &&
              <DialogContentText sx={{ fontWeight: "bold", paddingBottom: "1rem" }}>
                En st dzeye yeni bir Wbs eklemek zeresiniz.
              </DialogContentText>
            } */}

            <Box onClick={() => setError_for_wbsName(false)}>
              <TextField
                variant="standard"
                // InputProps={{ sx: { height:"2rem", fontSize: "1.5rem" } }}
                defaultValue={selectedWbs.name}
                margin="normal"
                id="wbsName"
                name="wbsName"
                autoFocus
                error={error_for_wbsName}
                helperText={error_for_wbsName ? errorText_for_wbsName : ""}
                // margin="dense"
                label="Wbs Ad"
                type="text"
                fullWidth
              />
            </Box>

            <Box onClick={() => setError_for_wbsCodeName(false)}>
              <TextField
                variant="standard"
                // InputProps={{ sx: { height:"2rem", fontSize: "1.5rem" } }}

                defaultValue={selectedWbs.codeName}
                margin="normal"
                id="wbsCodeName"
                name="wbsCodeName"
                // autoFocus
                error={error_for_wbsCodeName}
                helperText={error_for_wbsCodeName ? errorText_for_wbsCodeName : "rnek : KABA NAAT --> KAB"}
                // margin="dense"
                label="Wbs Kod Ad"
                type="text"
                fullWidth
              />
            </Box>

          </DialogContent>

          <DialogActions sx={{ padding: "1.5rem" }}>
            <Button onClick={() => setShow()}>ptal</Button>
            <Button type="submit">Gncelle</Button>
          </DialogActions>

        </Box>
      </Dialog>
    </div >
  );


}