import { useState } from 'react';
import { useApp } from "./useApp.js";
import deleteLastSpace from '../functions/deleteLastSpace.js';
import { DialogWindow } from '../components/general/DialogWindow';


//mui
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import { Typography } from '@mui/material';


export default function P_FormLbsCreate({ setShow, isProject, setIsProject, selectedLbs, setSelectedLbs }) {

  // proje ve _id si yoksa lbs oluturma formunu gstermenin bir anlam yok, hata vererek durduruyoruz
  if (!isProject?._id) {
    throw new Error("Lbs oluturulacak projenin database kayd iin ProjeId belirtilmemi, sayfay yeniden ykleyin, sorun devam ederse Rapor7/24 ile irtibata geiniz.")
  }

  const [showDialog, setShowDialog] = useState(false)
  const [dialogCase, setDialogCase] = useState("")

  const [error_for_lbsName, setError_for_lbsName] = useState(false)
  const [errorText_for_lbsName, setErrorText_for_lbsName] = useState()

  const [error_for_lbsCodeName, setError_for_lbsCodeName] = useState(false)
  const [errorText_for_lbsCodeName, setErrorText_for_lbsCodeName] = useState()

  const RealmApp = useApp();

  const callBack_m = () => setShow()



  async function handleSubmit(event) {

    event.preventDefault();
    let isError = false

    try {

      // girilen verileri alma ve sonlarndaki boluklar kaldrma
      const data = new FormData(event.currentTarget);
      const lbsName = deleteLastSpace(data.get('lbsName'))
      const lbsCodeName = deleteLastSpace(data.get('lbsCodeName'))

      // bu ksmda frontend ksmnda form validation hatalarn ilgili alanlarda gsterme ilemleri yaplr, aada backend de
      if (!lbsName) {
        setError_for_lbsName(true);
        setErrorText_for_lbsName("Zorunlu")
        isError = true
        console.log("lbsName", "yok -- error")
      }

      // bu ksmda frontend ksmnda form validation hatalarn ilgili alanlarda gsterme ilemleri yaplr, aada backend de
      if (!lbsCodeName) {
        setError_for_lbsCodeName(true);
        setErrorText_for_lbsCodeName("Zorunlu")
        isError = true
        console.log("lbsCodeName", "yok -- error")
      }

      if (lbsCodeName.includes(" ") ) {
        setError_for_lbsCodeName(true);
        setErrorText_for_lbsCodeName("Boluk kullanmaynz")
        isError = true
        console.log("lbsCodeName", "yok -- error")
      }

      // ilgili hatalar yukarda ilgili form alanlarna yazlm olmal
      // db ye sorgu yaplp db megul edilmesin diye burada durduruyoruz
      // frontendden gese bile db den errorFormObject kontrol yaplyor aada
      if (isError) {
        console.log("bu satrn altnda fonksiyon --return-- ile durduruldu")
        return
      }



      const newLbsItem = {
        projectId: isProject._id,
        upLbsId: selectedLbs ? selectedLbs._id : "0",
        newLbsName: lbsName,
        newLbsCodeName: lbsCodeName
      }

      const resultProject = await RealmApp.currentUser.callFunction("createLbs", newLbsItem);

      // eer gnderilen form verilerinde hata varsa db den gelen form validation mesajlar form iindeki ilgili alanlarda gsterilir ve fonksiyon durdurulur
      // yukarda da frontend kontrol yaplmt
      if (resultProject.errorFormObj) {

        const errorFormObj = resultProject.errorFormObj

        console.log("errorFormObj", errorFormObj)

        // baka form alanlar olsayd onlarnkini de ekleyecektik aadaki returnden nce, onlarda da hata uyarlarn grecektik
        if (errorFormObj.newLbsName) {
          setError_for_lbsName(true);
          setErrorText_for_lbsName(errorFormObj.newLbsName)
          isError = true
        }

        // baka form alanlar olsayd onlarnkini de ekleyecektik aadaki returnden nce, onlarda da hata uyarlarn grecektik
        if (errorFormObj.newLbsCodeName) {
          setError_for_lbsCodeName(true);
          setErrorText_for_lbsCodeName(errorFormObj.newLbsCodeName)
          isError = true
        }

        return
      }


      // _id yoksa istediimiz proje verisi deil demekki, hata ile durduruyoruz
      if (!resultProject._id) {
        throw new Error("db den Proje olarak beklenen verinin _id property yok, sayfay yenileyiniz, sorun devam ederse Rapor7/24 ile irtibata geiniz..")
      }


      // yukardaki yaplan _id kontrol tamamsa bu veri db de kaydolmu demektir, refetch() yapp db yi yormaya gerek yok
      setIsProject(resultProject)

      // sorgu ileminden nce seilen lbs varsa, temizliyoruz, en byk gereke seilen lbs silinmi olabilir, onunla ilem db de hata verir
      setSelectedLbs(null)

      setShow()

      return

      // setShowDialogSuccess("Lbs kayd baar ile gerekleti")

    } catch (err) {

      console.log(err)
      let hataMesaj_ = err?.message ? err.message : "Beklenmedik hata, sayfay yenileyiniz, sorun devam ederse Rapor7/24 ile irtibata geiniz.."

      let text1 = "__mesajBaslangic__"
      let text2 = "__mesajBitis__"
      let mesajBaslangic = hataMesaj_.includes(text1) ? hataMesaj_.indexOf(text1) + text1.length : 0
      let mesajBitis = hataMesaj_.includes(text2) ? hataMesaj_.indexOf(text2) : hataMesaj_.length
      // console.log(hataMesaj_.slice(mesajBaslangic + "mesajBaslangic:".length, mesajBitis))
      hataMesaj_ = hataMesaj_.slice(mesajBaslangic, mesajBitis)
      console.log(hataMesaj_)

      // eer ifte kayt oluyorsa form iindeki mahal ismi girilen yere aadaki mesaj gnderilir, fonksiyon durdurulur
      // form sayfas kapanmadan hata gsterimi
      if (hataMesaj_.includes("duplicate key error")) {
        setError_for_lbsName(true);
        setErrorText_for_lbsName("Ayn seviyede, ayn isimde lbs olamaz")
        console.log("Ayn seviyede, ayn isimde lbs olamaz")
        return
      }

      setDialogCase("error")
      setShowDialog(hataMesaj_)

    }

  }


  return (
    <div>

      {showDialog &&
        <DialogWindow dialogCase={dialogCase} showDialog={showDialog} setShowDialog={setShowDialog} afterDone={callBack_m} />
      }

      <Dialog
        PaperProps={{ sx: { width: "80%", position: "fixed", top: "10rem" } }}
        open={true}
        onClose={() => setShow()} >
        {/* <DialogTitle>Subscribe</DialogTitle> */}
        <Box component="form" onSubmit={handleSubmit} noValidate sx={{ mt: 1 }}>

          <DialogContent>

            <DialogContentText sx={{ fontWeight: "bold", paddingBottom: "1rem" }}>
              {/* <Typography sx> */}
              Lbs Olutur
              {/* </Typography> */}
            </DialogContentText>

            {selectedLbs &&
              <>
                <DialogContentText sx={{ fontWeight: "bold", paddingBottom: "1rem" }}>
                  {selectedLbs.code} {"-->"} {selectedLbs.name}
                </DialogContentText>
                <Typography >
                  bal altna yeni bir Lbs eklemek zeresiniz.
                </Typography>
              </>
            }

            {!selectedLbs &&
              <DialogContentText sx={{ fontWeight: "bold", paddingBottom: "1rem" }}>
                {/* <Typography > */}
                En st dzeye yeni bir Lbs eklemek zeresiniz.
                {/* </Typography> */}
              </DialogContentText>
            }

            <Box onClick={() => setError_for_lbsName(false)}>
              <TextField
                variant="standard"
                // InputProps={{ sx: { height:"2rem", fontSize: "1.5rem" } }}

                margin="normal"
                id="lbsName"
                name="lbsName"
                autoFocus
                error={error_for_lbsName}
                helperText={error_for_lbsName ? errorText_for_lbsName : ""}
                // margin="dense"
                label="Lbs Ad"
                type="text"
                fullWidth
              />
            </Box>

            <Box onClick={() => setError_for_lbsCodeName(false)}>
              <TextField
                variant="standard"
                // InputProps={{ sx: { height:"2rem", fontSize: "1.5rem" } }}

                margin="normal"
                id="lbsCodeName"
                name="lbsCodeName"
                // autoFocus
                error={error_for_lbsCodeName}
                helperText={error_for_lbsCodeName ? errorText_for_lbsCodeName : "rnek : KABA NAAT --> KAB"}
                // margin="dense"
                label="Lbs Kod Ad"
                type="text"
                fullWidth
              />
            </Box>

          </DialogContent>

          <DialogActions sx={{ padding: "1.5rem" }}>
            <Button onClick={() => setShow()}>ptal</Button>
            <Button type="submit">Olutur</Button>
          </DialogActions>

        </Box>
      </Dialog>
    </div >
  );


}